
























import { Component, Prop, Vue } from "vue-property-decorator";
import Button from "./../components/Button.vue";

@Component({
  components: {
    Button
  }
})
export default class ContentRow extends Vue {
  @Prop()
  icon!: string;

  @Prop({ default: "default" })
  btnVariation!: string;

  @Prop({ default: "Default" })
  btnTitle!: string;
}
