


























import { Component, Prop, Vue, Watch } from "vue-property-decorator";

@Component({})
export default class TextInput extends Vue {
  @Prop()
  name!: String;

  @Prop()
  value!: String;

  @Prop()
  error!: String;

  @Prop()
  helpText!: String;

  @Prop()
  type!: {
    type: String;
    default: "text";
  };

  @Prop()
  placeholder!: String;

  @Prop()
  disabled!: Boolean;

  @Prop()
  label!: String;

  content: String = this.value;

  handleInput(e: String) {
    this.$emit("input", this.content);
  }
}
