



























import { Component, Prop, Vue } from "vue-property-decorator";
import Button from "./../components/Button.vue";

@Component({
  components: {
    Button
  }
})
export default class ModalBasic extends Vue {
  @Prop()
  label!: string;

  @Prop()
  title!: string;

  @Prop()
  image!: string;

  get overlayImage() {
    return this.image;
  }
}
