





















import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class VitualItem extends Vue {
  @Prop()
  name!: String;

  @Prop()
  value!: String;

  @Prop()
  preview!: String;

  @Prop()
  quantity!: Number;

  @Prop()
  rarity!: String;

  @Prop({ default: false })
  selected!: Boolean;

  @Prop()
  selectionCount!: String;

  @Prop()
  type!: String;

  get virtualItemClasses() {
    let classes = [];

    if (this.rarity) {
      classes.push(`s-virtual-item--${this.rarity}`);
    }

    if (this.selected) {
      classes.push("is-selected");
    }

    return classes.join(" ");
  }
}
