













import { Component, Prop, Vue } from "vue-property-decorator";
import Button from "./Button.vue";

@Component({
  components: {
    Button
  }
})
export default class TooltipNotice extends Vue {
  @Prop({ required: true })
  title!: string;

  @Prop({ required: true })
  desc!: string;

  clickHandler() {
    this.$emit("handle-tooltip");
  }
}
